import React, { useRef, useEffect, useState } from 'react';
import * as THREE from 'three-full';
import './background.css';


const charList = ' .:-=+*#%@'.split("");;

const Loader = () => {
    let theCanvas = useRef();

    let [background, setBackground] = useState([]);
    let [windowWidth, setWindowWidth] = useState(0);
    let [windowHeight, setWindowHeight] = useState(0);

    useEffect(() => {
        const onWindowResize = () => {
            setWindowWidth(window.innerWidth);
            setWindowHeight(window.innerHeight);
        }
        setWindowWidth(window.innerWidth);
        setWindowHeight(window.innerHeight);
        window.addEventListener("resize", onWindowResize);
        return () => window.removeEventListener("resize", onWindowResize);
    }, []);

    useEffect(() => {
        if (windowWidth > 0 && windowHeight > 0) {
            let width, height;
    
            if (windowWidth > windowHeight) {
                width = 61; // 9:16
                height = 46; // 9:16
            } else {
                width = 38; // 4:3
                height = 68; // 4:3
            }
    
            let canvas = document.createElement( "canvas" );
            const scene = new THREE.Scene();
            const camera = new THREE.PerspectiveCamera(75, width / height, 1, 1000);
            const renderer = new THREE.WebGLRenderer({ canvas: canvas.current, antialias: true });
            //const geometry = new THREE.TorusKnotGeometry(10, 1, 64, 8, 2, 3, 1);
            const geometry = new THREE.TorusKnotGeometry(10, 1, 64, 8, 2, 3, 1);
            const material = new THREE.MeshNormalMaterial();
            const cube = new THREE.Mesh(geometry, material);
            const needResize = canvas.width !== width || canvas.height !== height;
            if (needResize) {
                renderer.setSize(width, height, false);
            }
            camera.position.z = 10;
            scene.add(cube);
    
            //let effect = new THREE.AsciiEffect( renderer, ' .:-+*=%@#', { invert: true } );
            
            //let effect = new AsciiEffect( renderer, ' .<+*=%@#', { invert: true } );
            
    
            
    
            //effect.setSize( width, height );
            //effect.domElement.style.color = 'black';
            //effect.domElement.style.backgroundColor = 'white';
            //theCanvas.current.appendChild( effect.domElement );
    
            
    
            const renderScene = () => {
                renderer.render(scene, camera);
                let virtualCanvas = renderer.domElement;
                var realCanvas = document.createElement( "canvas" );
                let context = realCanvas.getContext( "2d" );
                context.clearRect(0, 0, width, height);
    
                let overrides = [{value: "amaury", 
                                  x: 3, 
                                  y: 4, 
                                  first: true}, 
                                 {value: "gutierrez", 
                                  x: 3, 
                                  y: 6, 
                                  first: true},
                                 {value: "then", 
                                  x: width - "then".length - 3, 
                                  y: height - 3 * 2, 
                                  first: true,
                                  url: "https://then.gallery/"},
                                 {value: "instagram", 
                                  x: width - "instagram".length - 3, 
                                  y: height - 5 * 2, 
                                  first: true,
                                  url: "https://www.instagram.com/_amaurs/"}]
    
                context.drawImage(virtualCanvas, 0, 0, width, height);
    
                let frame = context.getImageData(0, 0, width, height).data;
                let rows = [];
                let first = true;
    
                for (let y = 0; y < height; y += 2 ) {
                    
                    let row = [];
                    let current = "";
    
                    for ( let x = 0; x < width; x ++ ) {
                        let flag = undefined;
    
                        if (y == overrides[0].y && overrides[0].x <= x && x < overrides[0].x + overrides[0].value.length) {
                            if (overrides[0].first) {
                                row.push(current);
                                row.push(<a className={"Loader-name" + (overrides[0].url?"-link":"")} href={overrides[0].url}>{overrides[0].value}</a>);
                                current = "";
                                overrides[0].first = false;  
                            }
                        } else if (y == overrides[1].y && overrides[1].x <= x && x < overrides[1].x + overrides[1].value.length) {
                            if (overrides[1].first) {
                                row.push(current);
                                row.push(<a className={"Loader-name" + (overrides[1].url?"-link":"")} href={overrides[1].url}>{overrides[1].value}</a>);
                                current = "";
                                overrides[1].first = false;  
                            }
                        } else if (y == overrides[2].y && overrides[2].x <= x && x < overrides[2].x + overrides[2].value.length) {
                            if (overrides[2].first) {
                                row.push(current);
                                row.push(<a className={"Loader-name" + (overrides[2].url?"-link":"")} href={overrides[2].url}>{overrides[2].value}</a>);
                                current = "";
                                overrides[2].first = false;  
                            }
                        } else if (y == overrides[3].y && overrides[3].x <= x && x < overrides[3].x + overrides[3].value.length) {
                            if (overrides[3].first) {
                                row.push(current);
                                row.push(<a className={"Loader-name" + (overrides[3].url?"-link":"")} href={overrides[3].url}>{overrides[3].value}</a>);
                                current = "";
                                overrides[3].first = false;  
                            }
                        }
                        else {
    
                            let offset = ( y * width + x ) * 4;
                            let red = frame[offset];
                            let green = frame[offset + 1];
                            let blue = frame[offset + 2];
                            let alpha = frame[offset + 3];
        
                            let brightness;
        
                            brightness = ( 0.3 * red + 0.59 * green + 0.11 * blue ) / 255;
        
                            let charIndex = Math.floor( ( 1 - brightness ) * ( charList.length - 1 ) );
        
                            if ( true ) {
    
                                charIndex = charList.length - charIndex - 1;
    
                            }
        
                            let currentChar = charList[charIndex];
        
                            if ( currentChar === undefined || currentChar == " " )
                                currentChar = " ";
            
                            if ( currentChar == "<" )
                                currentChar = "&lt;";
            
                            if ( currentChar == ">" )
                                currentChar = "&gt;";
        
                            current += currentChar;
                        }
                        
    
                    }
                    row.push(current);
                    rows.push(row);
                }
                setBackground(rows);
            }
            
            const animate = () => {
                cube.rotation.x += 0.01;
                cube.rotation.y += 0.01;
                renderScene();
                frameId = requestAnimationFrame(animate);
    
            }
    
            let frameId = requestAnimationFrame(animate);
            
            return () => {
                cancelAnimationFrame(frameId);
                frameId = null;
                scene.remove(cube);
                geometry.dispose();
                material.dispose();
            }
        }
    }, [windowWidth, windowHeight]);

    return (
        <pre className="Loader" >
            {background.map((row, rowIndex) => <div key={rowIndex}>{row.map((element, index) => <span key={index}>{element}</span>)}</div>)}
        </pre>
    );
}

export default Loader;